<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>
        <v-list-item v-if="docsReview.status.code !== 'approved'">
          <v-btn class="secondary" dark @click="$router.push('/questionnaire/nmcb-registration-application/nmcb-registration-application/' + queries.id +'?practitioner=' + queries.practitioner)">
            <v-icon light left>mdi-pencil</v-icon>
            <span>Edit Application</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="references.length < 2">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-reference-letter/nmcb-reference-letter?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Add Reference Letter</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="!transcript.id">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-transcript/nmcb-transcript?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Add Transcript</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="(application.trainLocation.code === 'outside' || application.registrationType.code === 'temporary') && !goodStanding.id">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-letter-good-standing/nmcb-letter-good-standing?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Add Letter Good Standing</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddHandlingFee">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-handling-fee/nmcb-handling-fee?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Add Handling Fee</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddHandlingFeeReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-handling-fee-review/nmcb-handling-fee-review-status?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="handlingFeeReview.status && handlingFeeReview.status.code !=='pending'">Change Status</span>
            <span v-else>Accept/Decline Handling Fee</span>
          </v-btn>
        </v-list-item>

        <v-list-item v-if="canAddRegistrationFee">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-registration-fee/nmcb-registration-fee?practitioner=' + queries.practitioner + '&application=' + queries.id)"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Add Registration Fee</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddRegistrationFeeReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-registration-fee-review/nmcb-registration-fee-review-status?practitioner=' + queries.practitioner + '&application=' + queries.id)"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="registrationFeeReview.status && registrationFeeReview.status.code !=='pending'">Change Status</span>
            <span v-else>Accept/Decline Registration Fee</span>
          </v-btn>
        </v-list-item>


        <v-list-item v-if="canAddCommitteeReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-reg-committee-review/nmcb-reg-committee-review?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="regCommitteeReview.status && regCommitteeReview.status.code !=='pending'">Change Committee Review</span>
            <span v-else>Add Committee Review</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddDocsReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-documents-review/nmcb-app-documents-review-status?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="docsReview.status && docsReview.status.code !=='pending'">Change Status</span>
            <span v-else>Accept/Decline Docs</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddBoardReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-board-review/nmcb-board-review?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="boardReview.status.code !=='pending'">Change Board Review</span>
            <span v-else>Add Board Review</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddExamFee">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-exam-fee/nmcb-exam-fee?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Add Exam Fee</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddExamFeeReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-exam-fee-review/nmcb-exam-fee-review-status?practitioner=' + queries.practitioner + '&application=' + queries.id + '&_redirect=custom/nmcb-registration-application?id=' + queries.id + '&path=/components/nmcb')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="examFeeReview.status && examFeeReview.status.code !=='pending'">Change Status</span>
            <span v-else>Accept/Decline Exam Fee</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-row>
      <v-col>
        <bw-page-view :id="queries.id" page="nmcb-registration-application" :key="appCompKey" />
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
          v-if="registration.id"
          >
          <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
            Registration
          </v-card-title>
          <v-card-text class="my-3">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Registration Number</span>
              </v-col>
              <v-col cols="8">
                {{ registration.regNumber }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Registration Status</span>
              </v-col>
              <v-col cols="8">
                {{ registration.status.display }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Registration Date</span>
              </v-col>
              <v-col cols="8">
                {{ registration.date }}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
          v-if="examResults.results.length > 0"
          >
          <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
            Exam Results
          </v-card-title>
          <v-card-text class="my-3">
            <div v-for="(result, index) in examResults.results" :key="index">
              <v-row class="justify-space-between">
                <v-col cols="4">
                  <span class="font-weight-bold">{{ result.exam }}:</span>
                </v-col>
                <v-col cols="8">
                  {{ result.score }}
                </v-col>
              </v-row>
              <v-divider/>
            </div>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Final Assessment:</span>
              </v-col>
              <v-col cols="8">
                <v-chip
                  dark
                  class="ma-2"
                  v-if="examResults.assessment === 'Fail'"
                  color="red"
                >
                  {{ examResults.assessment }}
                </v-chip>
                <v-chip
                  dark
                  class="ma-2"
                  v-if="examResults.assessment === 'Pass'"
                  color="green"
                >
                  {{ examResults.assessment }}
                </v-chip>
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Attempts:</span>
              </v-col>
              <v-col cols="8">
                <v-chip
                  dark
                  class="ma-2"
                  color="blue"
                >
                  {{ examResults.attempt }}
                </v-chip>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
        <bw-page-view v-if="references.length > 0" :id="references[0]" page="nmcb-reference-letter" />
        <bw-page-view v-if="references.length > 1" :id="references[1]" page="nmcb-reference-letter" />
        <bw-page-view v-if="transcript.id" :id="transcript.id" page="nmcb-transcript" />
        <bw-page-view v-if="goodStanding.id" :id="goodStanding.id" page="nmcb-letter-good-standing" />
        <bw-page-view v-if="registrationFee.id" :id="registrationFee.id" page="nmcb-registration-fee" />
        <bw-page-view v-if="registrationFeeReview.id" :id="registrationFeeReview.id" page="nmcb-registration-fee-review-status" />
        <bw-page-view v-if="handlingFee.id" :id="handlingFee.id" page="nmcb-handling-fee" />
        <bw-page-view v-if="handlingFeeReview.id" :id="handlingFeeReview.id" page="nmcb-handling-fee-review-status" />
        <bw-page-view v-if="regCommitteeReview.id" :id="regCommitteeReview.id" page="nmcb-reg-committee-review" />
        <bw-page-view v-if="docsReview.id" :id="docsReview.id" page="nmcb-app-documents-review-status" />
        <bw-page-view v-if="boardReview.id" :id="boardReview.id" page="nmcb-board-review" />
        <bw-page-view v-if="examFee.id" :id="examFee.id" page="nmcb-exam-fee" />
        <bw-page-view v-if="examFeeReview.id" :id="examFeeReview.id" page="nmcb-exam-fee-review-status" />
        <v-container class="my-3">
          <v-card
            class="mx-auto"
            max-width="700"
            outlined
            v-if="attachments.length > 0"
          >
            <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
              Attachments
            </v-card-title>
            <v-card-text class="my-3">
              <template v-for="attachment in attachments">
                <v-card :key="attachment.code">
                  <v-card-title>
                    {{ attachment.display }}
                  </v-card-title>
                  <v-card-text class="my-3">
                    <displayAttachment :data="attachment.data" :code="attachment.code" />
                  </v-card-text>
                </v-card>
              </template>
            </v-card-text>
          </v-card>
        </v-container>
      </v-col>
      <v-col cols="3">
        <br>
        <locally-trained-stepper
          :step="step"
          :noRefLetter1="noRefLetter1"
          :noRefLetter2="noRefLetter2"
          :noTranscript="noTranscript"
          v-if="application.trainLocation.code === 'local'"
        />
        <outside-trained-stepper
          :step="step"
          :noRefLetter1="noRefLetter1"
          :noRefLetter2="noRefLetter2"
          :noGoodStanding="noGoodStanding"
          :noTranscript="noTranscript"
          v-if="application.trainLocation.code === 'outside'"
        />
        <temporary-certificate-stepper
          :step="step"
          :noRefLetter1="noRefLetter1"
          :noRefLetter2="noRefLetter2"
          :noGoodStanding="noGoodStanding"
          :noTranscript="noTranscript"
          v-if="application.registrationType.code === 'temporary'"
        />
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
const querystring = require("querystring");
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      overlayUnclosed: false,
      loading: false,
      appCompKey: 'application'+0,
      application: {
        resource: {},
        trainLocation: {},
        statusCode: '',
        applicationStatus: {},
        registrationType: {}
      },
      docsReview: {
        id: '',
        status: ''
      },
      boardReview: {
        id: '',
        status: ''
      },
      examResults: {
        results: [],
        assessment: ''
      },
      references: [],
      transcript: {
        id: ''
      },
      goodStanding: {
        id: ''
      },
      handlingFee: {
        id: ''
      },
      handlingFeeReview: {
        id: '',
        status: ''
      },
      registrationFee: {
        id: ''
      },
      registrationFeeReview: {
        id: '',
        status: ''
      },
      regCommitteeReview: {
        id: '',
        status: ''
      },
      examFee: {
        id: ''
      },
      examFeeReview: {
        id: '',
        status: ''
      },
      registration: {
        id: ''
      },
      attachments: [],
      step: 2,
      noTranscript: true,
      noGoodStanding: true,
      noRefLetter1: true,
      noRefLetter2: true,
    }
  },
  methods: {
    getApplication() {
      this.step = 2
      fetch("/fhir/Basic/" + this.queries.id).then(response => response.json().then((application) => {
        let trainLocation = application.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-training-location"
        })
        if(trainLocation) {
          this.application.trainLocation = trainLocation.valueCoding 
        }
        this.application.resource = application
        this.application.applicationStatus = application.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-application-status'
        })?.valueCoding
        this.application.registrationType = application.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-registration-type'
        })?.valueCoding
        this.application.statusCode = this.application.applicationStatus?.code
        setTimeout(() => {
          this.getRefLetters()
        }, 500);
      }))
    },
    getRefLetters() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-reference-letter-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((refLetters) => {
        if(refLetters.entry && refLetters.entry.length > 0) {
          this.noRefLetter1 = false
          this.references.push(refLetters.entry[0].resource.id)
          if(refLetters.entry.length > 1) {
            this.noRefLetter2 = false
            setTimeout(() => {
              this.references.push(refLetters.entry[1].resource.id)
            }, 500);
          }
          setTimeout(() => {
            this.getTranscript()
          }, 1000);
        } else {
          this.getTranscript()
        }
      }))
    },
    getTranscript() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-transcript-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((transcript) => {
        if(transcript.entry && transcript.entry.length > 0) {
          this.noTranscript = false
          this.transcript.id = transcript.entry[0].resource.id
          if(this.application.trainLocation.code === 'outside' || this.application.registrationType.code === 'temporary') {
            this.step = 2
            setTimeout(() => {
              this.getLetterGoodStanding()
            }, 500);
          } else if(this.references.length > 1) {
            this.step = 5
            setTimeout(() => {
              this.getDocsReviewStatus()
            }, 500);
          } else {
            this.overlayUnclosed = false
          }
        } else {
          if(this.application.trainLocation.code === 'outside' || this.application.registrationType.code === 'temporary') {
            setTimeout(() => {
              this.getLetterGoodStanding()
            }, 500);
          } else {
            this.overlayUnclosed = false
          }
        }
      }))
    },
    getLetterGoodStanding() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-letter-good-standing-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((letter) => {
        if(letter.entry && letter.entry.length > 0) {
          this.noGoodStanding = false
          this.goodStanding.id = letter.entry[0].resource.id
          if(this.transcript.id && this.references.length > 1) {
            this.step = 6
            setTimeout(() => {
              this.getDocsReviewStatus()
            }, 500);
          } else {
            this.step = 2
            this.overlayUnclosed = false
          }
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getHandlingFee() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-handling-fee-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((fee) => {
        if(fee.entry && fee.entry.length > 0) {
          this.step = 8
          this.handlingFee.id = fee.entry[0].resource.id
          setTimeout(() => {
            this.getHandlingFeeReviewStatus()
          }, 500);
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getHandlingFeeReviewStatus() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-handling-fee-review-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.handlingFeeReview.id = review.entry[0].resource.id
          this.handlingFeeReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
          if(this.handlingFeeReview.status.code === "approved") {
            this.step = 9
          }
          setTimeout(() => {
            this.getRegCommitteeReview()
          }, 500);
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getTempRegistrationFee() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-registration-fee-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((fee) => {
        if(fee.entry && fee.entry.length > 0) {
          this.step = 8
          this.registrationFee.id = fee.entry[0].resource.id
          setTimeout(() => {
            this.getTempRegistrationFeeReviewStatus()
          }, 500);
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getTempRegistrationFeeReviewStatus() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-registration-fee-review-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.registrationFeeReview.id = review.entry[0].resource.id
          this.registrationFeeReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
          if(this.registrationFeeReview.status.code === "approved") {
            this.step = 9
          }
          setTimeout(() => {
            this.getRegistration()
          }, 500);
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getRegCommitteeReview() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-reg-committee-review-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.regCommitteeReview.id = review.entry[0].resource.id
          this.regCommitteeReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
          if(this.regCommitteeReview.status.code === "approved") {
            this.step = 10
            setTimeout(() => {
              this.getExamFee()
            }, 500);
          } else {
            this.overlayUnclosed = false
          }
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getDocsReviewStatus() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-documents-review-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.docsReview.id = review.entry[0].resource.id
          this.docsReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
          if(this.application.trainLocation.code === 'outside') {
            if(this.docsReview.status.code === 'approved') {
              this.step = 7
            }
            setTimeout(() => {
              this.getHandlingFee()
            }, 500);
          } else if( this.application.registrationType.code === 'temporary' ) {
            if(this.docsReview.status.code === 'approved') {
              this.step = 7
            }
            setTimeout(() => {
              this.getTempRegistrationFee()
            }, 500);
          } else {
            if(this.docsReview.status.code === 'approved') {
              this.step = 6
            }
            setTimeout(() => {
              this.getBoardReview()
            }, 500);
          }
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getBoardReview() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-board-review-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.boardReview.id = review.entry[0].resource.id
          this.boardReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
          if(this.boardReview.status.code === 'approved') {
            this.step = 7
            setTimeout(() => {
              this.getExamFee()
            }, 500);
          } else {
            this.overlayUnclosed = false
          }
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getExamFee() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-exam-fee-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((fee) => {
        if(fee.entry && fee.entry.length > 0) {
          if(this.application.trainLocation.code === 'outside') {
            this.step = 11
          } else {
            this.step = 8
          }
          this.examFee.id = fee.entry[0].resource.id
          setTimeout(() => {
            this.getExamFeeReviewStatus()
          }, 500);
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getExamFeeReviewStatus() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-exam-fee-review-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.examFeeReview.id = review.entry[0].resource.id
          this.examFeeReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
          if(this.examFeeReview.status.code === "approved") {
            if(this.application.trainLocation.code === 'outside') {
              this.step = 12
            } else {
              this.step = 9
            }
            setTimeout(() => {
              this.getExam()
            }, 500);
          }
        }
        this.overlayUnclosed = false
      }))
    },
    getExam() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _include: "Basic:nmcbscheduledexam",
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((exam) => {
        if(exam.entry && exam.entry.length > 0) {
          let results = exam.entry.find((entry) => {
            return entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/nmcb-practitioner-exam-profile")
          })
          let schedule = exam.entry.find((entry) => {
            return entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/nmcb-exam-scheduling-profile")
          })
          let assessments = schedule.resource.extension.filter((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-exam-assessment-type"
          })
          let bothAvailable = true
          for(let assessment of assessments) {
            let examResult = {}
            assessment = assessment.valueCoding
            let score = results.resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/" + assessment.code + "-score"
            })
            examResult.exam = assessment.display
            if(score) {
              examResult.score = score.valueDecimal
            } else {
              examResult.score = "-"
              bothAvailable = false
            }
            this.examResults.results.push(examResult)
          }
          if(bothAvailable) {
            if(this.application.trainLocation.code === 'outside') {
              this.step = 13
            } else {
              this.step = 10
            }
            setTimeout(() => {
              this.getRegistration()
            }, 500);
          }
          let assessment = results.resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/exam-assessment"
          })
          let attempt = results.resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/exam-attempt"
          })
          if(assessment) {
            this.examResults.assessment = assessment.valueCoding.display
          }
          if(attempt) {
            this.examResults.attempt = attempt.valueInteger
          }
        }
      }))
    },
    getRegistration() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-registration-profile',
        nmcbapplication: 'Basic/' + this.queries.id,
        _include: "Basic:nmcbscheduledexam",
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((registration) => {
        if(registration.entry && registration.entry.length > 0) {
          this.registration.id = registration.entry[0].resource.id
          this.registration.regNumber = registration.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-number"
          })?.valueString
          this.registration.status = registration.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-registration-status"
          })?.valueCoding
          this.registration.date = registration.entry[0].resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-date"
          })?.valueDate
          if(this.registration.date) {
            let date = this.registration.date.split("-")
            this.registration.date = date[2] + "-" + date[1] + "-" + date[0]
          }
          if(this.application.trainLocation.code === 'outside') {
            this.step = 12
          } else if(this.application.registrationType.code === 'temporary') {
            this.step = 8
          } else {
            this.step = 9
          }
          this.overlayUnclosed = false
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getAttachments() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-application-attachment-profile',
        nmcbapplication: 'Basic/' + this.queries.id
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then(async(attachments) => {
        if(attachments.entry && attachments.entry.length > 0) {
          let counter = 0
          for(let ext of attachments.entry[0].resource.extension) {
            if(ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-professional-certificate") {
              let data = ext.extension.find((prof) => {
                return prof.url === "attachment"
              })?.valueAttachment
              this.attachments.push({
                code: "nmcb-professional-certificate" + ++counter,
                display: "Professional Certificate",
                data
              })
            } else if(ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-application-foreigner-attachment") {
              let name = ext.extension.find((foreigner) => {
                return foreigner.url === "name"
              })?.valueCoding
              let data = ext.extension.find((foreigner) => {
                return foreigner.url === "attachment"
              })?.valueAttachment
              this.attachments.push({
                code: name.code,
                display: name.display,
                data: data
              })
            }else if(ext.extension) {
              let name = ext.extension.find((ext) => {
                return ext.url === "name"
              }).valueCoding
              let data = ext.extension.find((ext) => {
                return ext.url === "attachment"
              })?.valueAttachment
              let attachment = {
                code: name.code,
                display: name.display
              }
              if(data) {
                attachment.data = data
              } 
              if(!name.display) {
                attachment.display = await this.$fhirutils.codeLookup(name.system, name.code)
              }
              this.attachments.push(attachment)
            }
          }
        }
      }))
    },
  },
  watch: {
    overlayUnclosed(val) {
      if(!val) {
        setTimeout(() => {
          this.getAttachments()
        }, 500);
      }
    }
  },
  computed: {
    canAddDocsReview() {
      if(this.application.trainLocation.code === 'local') {
        if(this.docsReview.status && this.docsReview.status.code !== 'approved' && this.transcript.id && this.references.length === 2) {
          return true
        }
      }
      if(this.application.trainLocation.code === 'outside' || this.application.registrationType.code === 'temporary') {
        if(this.docsReview.status && this.docsReview.status.code !== 'approved' && this.transcript.id && this.goodStanding.id && this.references.length === 2) {
          return true
        }
      }
      return false
    },
    canAddRegistrationFee() {
      if(this.application.registrationType.code !== 'temporary') {
        return false
      }
      if(this.goodStanding.id && this.transcript.id && this.docsReview.status.code === 'approved' && (!this.registrationFee.id || this.registrationFeeReview.status.code === 'pending' || this.registrationFeeReview.status.code === 'rejected')) {
        return true
      }
      return false
    },
    canAddRegistrationFeeReview() {
      if(this.registrationFee.id && this.registrationFeeReview.status.code !== "approved" && this.application.registrationType.code === 'temporary') {
        return true
      }
      return false
    },
    canAddHandlingFee() {
      if(this.application.trainLocation.code !== 'outside') {
        return false
      }
      if(this.goodStanding.id && this.transcript.id && this.docsReview.status.code === 'approved' && (!this.handlingFee.id || this.handlingFeeReview.status.code === 'pending' || this.handlingFeeReview.status.code === 'rejected')) {
        return true
      }
      return false
    },
    canAddHandlingFeeReview() {
      if(this.application.trainLocation.code !== 'outside') {
        return false
      }
      if(this.handlingFee.id && this.handlingFeeReview.status.code !== "approved") {
        return true
      }
      return false
    },
    canAddCommitteeReview() {
      if(this.application.trainLocation.code !== 'outside') {
        return false
      }
      if(this.handlingFeeReview.status.code === 'approved' && this.regCommitteeReview.status.code !== 'approved') {
        return true
      }
      return false
    },
    canAddBoardReview() {
      if(this.application.trainLocation.code !== 'local') {
        return false
      }
      if(this.docsReview.status.code === 'approved' && this.boardReview.status.code !== 'approved') {
        return true
      }
      return false
    },
    canAddExamFee() {
      if(this.application.trainLocation.code === 'local') {
        if(this.boardReview.status.code === 'approved' && (!this.examFee.id || this.examFeeReview.status.code === 'pending'|| this.examFeeReview.status.code === 'rejected')) {
          return true
        }
      } else if(this.application.trainLocation.code === 'outside') {
        if(this.regCommitteeReview.status.code === 'approved' && (!this.examFee.id || this.examFeeReview.status.code === 'pending'|| this.examFeeReview.status.code === 'rejected')) {
          return true
        }
      }
      return false
    },
    canAddExamFeeReview() {
      if(this.examFee.id && this.examFeeReview.status.code !== "approved") {
        return true
      }
      return false
    },
  },
  created() {
    this.overlayUnclosed = true
    this.getApplication()
  },
  components: {
    "bw-page-view": () => import("@/site/view/bw-page-view"),
    "locally-trained-stepper": () => import("./LocallyTrainedStepper"),
    "outside-trained-stepper": () => import("./OutsideTrainedStepper"),
    "temporary-certificate-stepper": () => import("./TemporaryCertificateStepper"),
    "displayAttachment": () => import("../../components/displayAttachment")
  }
}
</script>